import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';

import menuIcon from '../../assets/img/menu.png';
import contactsIcon from '../../assets/img/contacts.png';
import deliveryIcon from '../../assets/img/delivery.png';
import vacancyIcon from '../../assets/img/vacancy.png';
import { chatIds } from '../../common/access';

const Main = () => {
  const linkClassName =
    'w-[80%] h-20 flex justify-start pl-5 items-center mb-5 bg-silver dark:bg-darkGray rounded-lg';

  const userId = window.Telegram?.WebApp.initDataUnsafe.user.id;

  useEffect(() => {
    try {
      const WebApp = window.Telegram?.WebApp;
      if (!WebApp) {
        console.error('Telegram WebApp API .');
        return;
      }
      WebApp.ready();
      WebApp.expand();
      WebApp.requestFullscreen();
    } catch (error) {
      console.error('   Telegram WebApp:', error);
    }
  }, []);

  return (
    <div
      className="p-2 w-full h-full pt-20 flex flex-col justify-start items-center bg-gray-100 min-h-screen fixed"

    >
      {chatIds.includes(userId) ? (
        <>
          <Link
            to="/menu"
            onClick={() => window.Telegram.WebApp.HapticFeedback.impactOccurred('medium')}
            className={linkClassName}
          >
            <img src={menuIcon} className="h-5 mr-3" alt="menu" />
            <span className="dark:text-white"></span>
          </Link>
          <Link
            to="/contacts"
            onClick={() => window.Telegram.WebApp.HapticFeedback.impactOccurred('medium')}
            className={linkClassName}
          >
            <img src={contactsIcon} className="h-5 mr-3" alt="contacts" />
            <span className="dark:text-white"></span>
          </Link>
          <Link
            to="/delivery"
            onClick={() => window.Telegram.WebApp.HapticFeedback.impactOccurred('medium')}
            className={linkClassName}
          >
            <img src={deliveryIcon} className="h-5 mr-3" alt="delivery" />
            <span className="dark:text-white"></span>
          </Link>
          <Link
            to="/vacancies"
            onClick={() => window.Telegram.WebApp.HapticFeedback.impactOccurred('medium')}
            className={linkClassName}
          >
            <img src={vacancyIcon} className="h-5 mr-3" alt="vacancies" />
            <span className="dark:text-white"></span>
          </Link>
        </>
      ) : (
        <span> ,    </span>
      )}
    </div>
  );
};

export default Main;